html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

@font-face {
  font-family: "Roboto";
  src: url("./assets/fonts/roboto/Roboto-Regular.ttf"); }

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f4fb !important; }

#root {
  width: 100%;
  height: 100%; }

html::-webkit-scrollbar {
  display: none; }

html::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #e6221a, #ff125b); }

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: #F5F5F5; }

html {
  scrollbar-color: #e61f1d #fff;
  scrollbar-width: 2px; }

.Stations_Top_Header {
  background-color: #f3f4fb;
  color: #666666;
  padding-bottom: 2%; }

.Stations_Top_Title {
  display: flex;
  width: 50%;
  font-weight: 500;
  padding-left: 18px; }

.add-station {
  display: inline-block;
  font-weight: 500;
  padding-left: 18px; }

.addstation_title {
  padding-left: 0%; }

.Add_station_button {
  display: flex;
  width: 50%;
  justify-content: flex-end; }

.display-flex {
  display: flex; }

.pad-15l {
  padding-left: 15.5px; }

.pad-5b {
  padding-bottom: 5px; }

.gobackIcon {
  position: absolute;
  height: 15px;
  width: 15px;
  transform: rotate(90deg); }

.button_add_icon {
  padding-right: 25px; }

.button_add {
  display: inline;
  border: none;
  color: wheat;
  background-color: transparent; }

.addIcon_Station {
  height: 20px;
  width: 20px; }

.station-main-div {
  padding: 16px 17.8px 0px 15.5px; }

.table-style {
  border-spacing: unset;
  width: 100%;
  border-collapse: collapse; }

.tr-th-style {
  height: 40px;
  background-color: #f8f8f8;
  font-size: 12px;
  line-height: 1.33;
  color: #666666;
  text-align: left;
  padding-left: 19px; }

.tr-td-style {
  height: 60px;
  vertical-align: unset;
  font-size: 12px;
  line-height: 1.17;
  color: #000000;
  border-top: 1px solid #e7e8f7; }

.th-table:first-child,
.td-table:first-child {
  padding-left: 19px;
  padding-right: 10px; }

.td-top {
  padding-top: 13px; }

.station-table-text {
  padding-top: 5px;
  color: #999999; }

.sin {
  font-size: 12px;
  font-weight: 500;
  line-height: 1.17;
  color: #e61f1d; }

.tr-td-style:hover {
  background-color: #f8f8f8; }

.icon-div {
  display: none; }

.tr-td-style:hover .icon-div {
  display: block;
  position: absolute;
  padding-top: 1%; }

.tr-td-style:hover .icon-show {
  display: none; }

.fnt-weight {
  font-weight: normal; }

.btn-style {
  display: inline-block; }

.width-23 {
  width: 23%; }

.width-20 {
  width: 20%; }

.width-30 {
  width: 30%; }

.width-40 {
  width: 40%; }

.width-50 {
  width: 50% !important; }

.width-15 {
  width: 15%; }

.pad-3 {
  padding-right: 3%; }

.width-10 {
  width: 10%; }

.pad-16l {
  padding-left: 16px; }

.pad-18r {
  padding-right: 18px; }

.pad-0l {
  padding-left: 0px; }

.cursor-pntr {
  cursor: pointer; }
