html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

@font-face {
  font-family: "Roboto";
  src: url("./assets/fonts/roboto/Roboto-Regular.ttf"); }

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f4fb !important; }

#root {
  width: 100%;
  height: 100%; }

html::-webkit-scrollbar {
  display: none; }

html::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #e6221a, #ff125b); }

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: #F5F5F5; }

html {
  scrollbar-color: #e61f1d #fff;
  scrollbar-width: 2px; }

.station-text {
  font-size: 14px;
  line-height: 1.14;
  color: #666666;
  padding-bottom: 10px; }

.station-datalist {
  width: 33%;
  display: inline-block; }

.station-name {
  width: 67%;
  padding-left: 12.3px;
  display: inline-block; }

.pad-12l {
  padding-left: 12.3px; }

.width-34 {
  width: 34%; }

.width-25 {
  width: 25%; }

.width-25 {
  width: 25%; }

.toggle-dm-style {
  width: 112px;
  height: 100%; }

.pad-10 {
  padding: 10px; }

.pad-20b {
  padding-bottom: 20px; }

.CLC-station {
  width: 163px;
  padding-left: 16px;
  display: inline-block; }

.Region {
  width: 329px;
  padding-left: 14px;
  display: inline-block; }

.city {
  width: 517px;
  display: inline-block; }

.station-toggle {
  width: 146px;
  display: inline-block;
  padding-left: 16px; }

.pad-9l {
  padding-left: 9px; }

.pad-26b {
  padding-bottom: 26px; }

.Acc_Class {
  height: 51px;
  width: 100%;
  background-color: #f8f8f8;
  border: none;
  display: flex;
  align-items: center;
  padding: 0px 17.8px 0px 15.5px;
  background-color: #fff;
  border-top: 1px solid #e7e8f7; }

.acc-active {
  background-color: #f8f8f8;
  border-top: none; }

.ACC_Name {
  border: none; }

.acc-chevron {
  margin-left: auto; }

.example-enter {
  height: 0px;
  overflow: hidden; }

.example-leave {
  height: 0px;
  overflow: hidden; }

.example-enter.example-enter-active {
  height: 350px;
  transition: height .7s ease; }

.example-leave.example-leave-active {
  height: 0px;
  transition: height .6s ease-in-out; }

.Rectangle-116 {
  width: 100%;
  height: 57px;
  border: solid 1px #e7e8f7;
  background-color: #f8f8f8;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-size: 14px; }

.station-datalist input[type=text]:disabled {
  opacity: 1;
  cursor: not-allowed; }

.none input[type=text]:disabled {
  opacity: 1;
  cursor: not-allowed; }
