html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

@font-face {
  font-family: "Roboto";
  src: url("./assets/fonts/roboto/Roboto-Regular.ttf"); }

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f4fb !important; }

#root {
  width: 100%;
  height: 100%; }

html::-webkit-scrollbar {
  display: none; }

html::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #e6221a, #ff125b); }

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: #F5F5F5; }

html {
  scrollbar-color: #e61f1d #fff;
  scrollbar-width: 2px; }

.tab-main-div {
  height: 100%;
  width: 21%;
  position: fixed;
  margin-top: 0px;
  padding-left: 0px; }

.main-tab {
  padding: 30px 30px 25px 29px; }

.tab-panel {
  margin-left: 27.1%;
  background-color: #fff; }

.tablist {
  list-style-type: none;
  height: 48.5px;
  font-size: 14px;
  font-weight: 600;
  color: #e61f1d;
  display: flex;
  align-items: center;
  padding-left: 28px;
  border: 1px solid #e7e8f7;
  border-bottom: none;
  background-color: #fff;
  cursor: pointer; }

.tablist:last-child {
  border-bottom: 1px solid #e7e8f7; }

.react-tabs__tab--selected {
  background-color: #f8f8f8;
  color: #000000; }

.tab-head-div {
  color: #666666;
  padding-left: 10%;
  padding-bottom: 6%;
  padding-top: 1%; }
